<script>
import {
  // GetGoodsGroupList,
  UnShelvesGoods,
  updateNewStatus,
  updateRecommendStatus,
  updatePublishStatus
} from "@/api/product";
import {
  fetchList as fetchSkuStockList,
  update as updateSkuStockList
} from "@/api/skuStock";
import { fetchList as fetchProductAttrList } from "@/api/productAttr";
import { fetchListWithChildren } from "@/api/productCate";

const defaultListQuery = {
  keyword: null,
  pageNum: 1,
  pageSize: 5,
  publishStatus: null,
  verifyStatus: null,
  productSn: null,
  productCategoryId: null,
  brandId: null
};
var SuppliersID=JSON.parse(localStorage.getItem("userInfoS")).SuppliersID;

const option = {
  KeyWords: "", //查询关键字
  Offset: 0, //查询起始行
  Rows: 100, //查询条数
  IsShelves: '', //是否上架（0.下架；1.上架）
  IsRecommend: "", //是否推荐（0.否；1.是）
  IsNewGoods: "", //是否新品（0.否；1.是）
  IsHot: "", //是否热门（0.否；1.是）
  GoodsGroupID: "", //商品大类ID
  SortKey: 2,
  SupplierID:SuppliersID,
  SerialNumber:'',
  // SortKey
  // 1 上架时间升序
  // 2 上架时间降序
  // 3价格升序
  // 4价格降序
  // 5默认升序
  // 6 默认降序
};
export default {
  name: "productList",
  data() {
    return {
      editSkuInfo: {
        dialogVisible: false,
        productId: null,
        productSn: "",
        productAttributeCategoryId: null,
        stockList: [],
        productAttr: [],
        keyword: null
      },
      SortKey:[
        {label:'上架时间升序',value:1},
        {label:'上架时间降序',value:2},
        {label:'价格升序',value:3},
        {label:'价格降序',value:4},
        {label:'默认升序',value:5},
        {label:'默认降序',value:6},
      ],
      operates: [
        {
          label: "商品上架",
          value: "publishOn"
        },
        {
          label: "商品下架",
          value: "publishOff"
        },
        {
          label: "设为推荐",
          value: "recommendOn"
        },
        {
          label: "取消推荐",
          value: "recommendOff"
        },
        {
          label: "设为新品",
          value: "newOn"
        },
        {
          label: "取消新品",
          value: "newOff"
        },
        {
          label: "转移到分类",
          value: "transferCategory"
        },
        {
          label: "移入回收站",
          value: "recycle"
        }
      ],
      operateType: null,
      listQuery: Object.assign({}, defaultListQuery),
      list: null,
      total: null,
      listLoading: true,
      selectProductCateValue: null,
      multipleSelection: [],
      productCateOptions: [],
      brandOptions: [],
      publishStatusOptions: [
        {
          value: 1,
          label: "上架"
        },
        {
          value: 0,
          label: "下架"
        }
      ],
      RecommendStatusOptions: [
        //推荐
        {
          value: 1,
          label: "推荐"
        },
        {
          value: 0,
          label: "不推荐"
        }
      ],
      NewGoodsStatusOptions: [
        //是否新品
        {
          value: 1,
          label: "是"
        },
        {
          value: 0,
          label: "否"
        }
      ],
      HotStatusOptions: [
        //是否热门
        {
          value: 1,
          label: "是"
        },
        {
          value: 0,
          label: "否"
        }
      ],
      verifyStatusOptions: [
        {
          value: 1,
          label: "审核通过"
        },
        {
          value: 0,
          label: "未审核"
        }
      ],
      options: Object.assign({}, option),
      pageList: {
        pages: 1
      }
    };
  },
  created() {
    let IsShelves=this.$route.query.IsShelves;
    if(IsShelves||IsShelves==0){
      this.options.IsShelves=Number(IsShelves);
    }
    this.getList();
    this.getProductCateList();
  },
  watch: {
    // selectProductCateValue: function(newValue) {
    //   if (newValue != null && newValue.length == 2) {
    //     this.options.GoodsGroupID = newValue[1];
    //   } else {
    //     this.options.GoodsGroupID = "";
    //   }
    // }
  },
  filters: {
    filteTime(value){
      if(value.indexOf("T")>-1){
        return value.replace(/T/g,' ')
      }else{
        return value;
      }
    },
    verifyStatusFilter(value) {
      if (value === 1) {
        return "审核通过";
      } else {
        return "未审核";
      }
    }
  },
  methods: {
    ChangeOnSelect(value){
      this.options.GoodsGroupID=value[value.length-1];
    },
    getProductSkuSp(row, index) {
      if (index === 0) {
        return row.sp1;
      } else if (index === 1) {
        return row.sp2;
      } else {
        return row.sp3;
      }
    },
    getList() {
      this.listLoading = true;
      this.$request.GetGoodsList(this.options).then(res => {
        this.listLoading = false;
        this.list = res.content.GoodsModel;
        this.total = res.content.PageCount;
      });
    },
    getProductCateList() {
      //获取全部类
      this.$request.GetGoodsGroupList("").then(response => {
        let list = response.content;
        this.listLoading = false;
        let DataList= list.filter(x => {
          return x.Level == 1;
        });
        //筛选出等级1 的
        DataList.forEach(v => {
          v.children = [];
          for (let i = 0; i < list.length; i++) {
            if (list[i].Level == 1) continue;
            if (list[i].ParentID == v.ID) {
              v.children.push(list[i]);
            }
          }
        });
       //选出Id等于外部ParentID的为children
        for (let i = 0; i < DataList.length; i++) {
          let children = [];
          if (DataList[i].children != null && DataList[i].children.length > 0) {
            for (let j = 0; j < DataList[i].children.length; j++) {
              children.push({
                label: DataList[i].children[j].Name,
                value: DataList[i].children[j].ID
              });
            }
          }
          this.productCateOptions.push({
            label: DataList[i].Name,
            value: DataList[i].ID,
            children: children
          });
        }
      });
    },
    handleShowSkuEditDialog(index, row) {
      this.editSkuInfo.dialogVisible = true;
      this.editSkuInfo.productId = row.id;
      this.editSkuInfo.productSn = row.productSn;
      this.editSkuInfo.productAttributeCategoryId =
        row.productAttributeCategoryId;
      this.editSkuInfo.keyword = null;
      fetchSkuStockList(row.id, {
        keyword: this.editSkuInfo.keyword
      }).then(response => {
        this.editSkuInfo.stockList = response.data;
      });
      fetchProductAttrList(row.productAttributeCategoryId, {
        type: 0
      }).then(response => {
        this.editSkuInfo.productAttr = response.data.list;
      });
    },
    handleSearchEditSku() {
      fetchSkuStockList(this.editSkuInfo.productId, {
        keyword: this.editSkuInfo.keyword
      }).then(response => {
        this.editSkuInfo.stockList = response.data;
      });
    },
    // handleEditSkuConfirm() {
    //   if (
    //     this.editSkuInfo.stockList == null ||
    //     this.editSkuInfo.stockList.length <= 0
    //   ) {
    //     this.$message({
    //       message: "暂无sku信息",
    //       type: "warning",
    //       duration: 1000
    //     });
    //     return;
    //   }
    //   this.$confirm("是否要进行修改", "提示", {
    //     confirmButtonText: "确定",
    //     cancelButtonText: "取消",
    //     type: "warning"
    //   }).then(() => {
    //     updateSkuStockList(
    //       this.editSkuInfo.productId,
    //       this.editSkuInfo.stockList
    //     ).then(response => {
    //       this.$message({
    //         message: "修改成功",
    //         type: "success",
    //         duration: 1000
    //       });
    //       this.editSkuInfo.dialogVisible = false;
    //     });
    //   });
    // },
    handleSearchList() {
      //获得查询结果
      this.handleCurrentChange(1)
    },
    handleBatchOperate() {
      if (this.operateType == null) {
        this.$message({
          message: "请选择操作类型",
          type: "warning",
          duration: 1000
        });
        return;
      }
      if (this.multipleSelection == null || this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择要操作的商品",
          type: "warning",
          duration: 1000
        });
        return;
      }
      this.$confirm("是否要进行该批量操作?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        let ids = [];
        for (let i = 0; i < this.multipleSelection.length; i++) {
          ids.push(this.multipleSelection[i].id);
        }
        switch (this.operateType) {
          case this.operates[0].value:
            this.updatePublishStatus(1, ids);
            break;
          case this.operates[1].value:
            this.updatePublishStatus(0, ids);
            break;
          case this.operates[2].value:
            this.updateRecommendStatus(1, ids);
            break;
          case this.operates[3].value:
            this.updateRecommendStatus(0, ids);
            break;
          case this.operates[4].value:
            this.updateNewStatus(1, ids);
            break;
          case this.operates[5].value:
            this.updateNewStatus(0, ids);
            break;
          case this.operates[6].value:
            break;
          case this.operates[7].value:
            this.updateDeleteStatus(1, ids);
            break;
          default:
            break;
        }
        this.getList();
      });
    },
    // handleSizeChange(val) {
    //   console.log(val);
    //   this.options.pageNum = 1;
    //   this.options.pageSize = val;
    //   this.getList();
    // },
    handleCurrentChange(val) {
      this.pageList.pages = val;
      this.options.Offset = (val - 1) * this.options.Rows;
      this.getList();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handlePublishStatusChange(index, row) {
      // let ids = [];
      // ids.push(row.id);
      // this.updatePublishStatus(row.publishStatus, ids);
      UnShelvesGoods(row.GoodsID).then(res => {
        if (res == "SUCCESS") {
          this.$message({
            message: "操作成功",
            showClose: true,
            type: "success"
          });
        } else {
          this.$message({
            message: "操作失败",
            showClose: true,
            type: 'error'
          });
        }
      });
    },
    handleHotStatusChange(index, row) {},
    handleNewStatusChange(index, row) {
      let ids = [];
      ids.push(row.id);
      this.updateNewStatus(row.newStatus, ids);
    },
    handleRecommendStatusChange(index, row) {
      let ids = [];
      ids.push(row.id);
      this.updateRecommendStatus(row.recommandStatus, ids);
    },
    handleResetSearch() {
      this.selectProductCateValue = [];
      this.options = Object.assign({}, option);
    },
    // handleDelete(index, row) {
    //   this.$confirm("是否要进行删除操作?", "提示", {
    //     confirmButtonText: "确定",
    //     cancelButtonText: "取消",
    //     type: "warning"
    //   }).then(() => {
    //     let ids = [];
    //     ids.push(row.id);
    //     this.updateDeleteStatus(1, ids);
    //   });
    // },
    handleUpdateProduct(index, row) {
      this.$router.push({
        path: "/pms/ProductDetails",
        query: {
          id: row.GoodsID
        }
      });
    },
    // handleShowProduct(index, row) {
    //   console.log("handleShowProduct", row);
    // },
    // handleShowVerifyDetail(index, row) {
    //   console.log("handleShowVerifyDetail", row);
    // },
    updatePublishStatus(publishStatus, ids) {
      //修改状态
      let params = new URLSearchParams();
      params.append("ids", ids);
      params.append("publishStatus", publishStatus);
      updatePublishStatus(params).then(response => {
        this.$message({
          message: "修改成功",
          type: "success",
          duration: 1000
        });
      });
    },
    updateNewStatus(newStatus, ids) {
      let params = new URLSearchParams();
      params.append("ids", ids);
      params.append("newStatus", newStatus);
      updateNewStatus(params).then(response => {
        this.$message({
          message: "修改成功",
          type: "success",
          duration: 1000
        });
      });
    },
    updateRecommendStatus(recommendStatus, ids) {
      let params = new URLSearchParams();
      params.append("ids", ids);
      params.append("recommendStatus", recommendStatus);
      updateRecommendStatus(params).then(response => {
        this.$message({
          message: "修改成功",
          type: "success",
          duration: 1000
        });
      });
    },
    updateDeleteStatus(deleteStatus, ids) {
      let params = new URLSearchParams();
      params.append("ids", ids);
      params.append("deleteStatus", deleteStatus);
      updateDeleteStatus(params).then(response => {
        this.$message({
          message: "删除成功",
          type: "success",
          duration: 1000
        });
      });
      this.getList();
    }
  }
};
</script>
<style scoped>
.pagination-container{
  margin-bottom: 10px !important;
}
</style>



